.container{
  max-width: 1170px;
  padding: 30px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
}
.Loader{
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  background: #222;
}
.loader-wrapper{
  width: 200px;
  height: 200px;
  margin: 20% auto 0 auto;  
}
.loader-inner{
  border-left: 5px solid #00d8ff;
  border-right: 5px solid #00d8ff;
  border-top: 5px solid #3ae9b0;
  border-bottom: 5px solid #3ae9b0;
  border-radius: 50%;
  height: 80px;
  width: 80px;
  position: relative;
  -webkit-animation: spin 3s linear infinite;
          animation: spin 3s linear infinite;
}
.loader-inner img{
  height: 50px;
  width: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.App .hero{
  background: #222 url(../img/316963.jpg)  no-repeat center center;
  background-size: 100% auto;
  color: #00d8ff;
  text-align: center;
  text-transform: uppercase;
  font-size: 200%;
  padding: 16% 0;
  position: relative;
}

.App .hero .hero-overlay .img img{
  height: auto;
  width: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);

}

.App .hero .hero-overlay .img{
  margin: 8% auto 0 auto;
  background: #00d8ff;
  border-radius: 30%;
  height: 140px;
  width: 140px;
  position: relative;
  -webkit-animation: animate 3s linear infinite;
          animation: animate 3s linear infinite;
}

.App .hero .hero-overlay{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(20, 20, 20, 0.6);
  z-index: 1;
}
.App .hero .hero-overlay h1{
  margin-top:2%;
}
.dataItem{
  color: #aaa;
  background: #333;
  border-bottom: 3px solid #00d8ff;
  transition: all .5s linear;
  -webkit-transition: all .5s linear;
  -moz-transition: all .5s linear;
  -o-transition: all .5s linear;
  -ms-transition: all .5s linear;
}

.dataItem img{
  background: #353535;
  border-bottom: 2px solid #222;
}

.dataItem .dataTitle{
  padding: 10px;
}

footer{
  padding: 60px 0;
  background: #111 url(../img/timeless.svg);
  background-size: 100px auto;
  border-top: 5px solid #333;
}

footer .container{
  grid-template-columns: repeat(1, 1fr);
  text-align: center;
  color: #888;
}

